@primary-color: #1890ff; // 
@second-color: #BBDEFB; // 
@link-color: #1890ff; // 
@success-color: #52c41a; // 
@warning-color: #faad14; // 
@error-color: #f5222d; // 
@font-size-base: 14px; // 
@heading-color: rgba(0, 0, 0, 0.85); // 
@text-color: rgba(0, 0, 0, 0.65); // 
@text-color-secondary : rgba(0, 0, 0, .45); // 
@disabled-color : rgba(0, 0, 0, .25); // 
@border-radius-base: 4px; // /
@border-color-base: #d9d9d9; // 
@box-shadow-base: 0 2px 8px rgba(0, 0, 0, 0.15); // 
@box-shadow-bottom: 0px 2px 4px -2px #ccc; // 
.root {
  position: relative;
  height: 100%;
}
.layout {
  height: 100%;
}
.header {
  display: flex;
  padding: 0 10px;
  align-items: center;
  background-color: #fff;
  box-shadow: 0px 2px 4px -2px #ccc;
}
.body {
  flex: 1;
  padding: 30px;
}
.project {
  margin-top: 20px;
  width: calc(50% - 10px);
}
.footer {
  padding: 0;
}
.terminal {
  position: absolute;
  top: 64px;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 5;
  user-select: none;
  background-color: #232527;
  &.hide {
    opacity: 0;
  }
}
